import React, { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { ConfirmationModal } from "../Modal/ConfirmationModal";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { ReactSearchAutocomplete } from "react-search-autocomplete";
import { BsPencilSquare } from "react-icons/bs";
import { useDispatch, useSelector } from "react-redux";
import {
  createPatientPharmacy,
  deletePatientPharmacy,
  getPatientPharmacyData,
} from "../../app/features/patientPharmacy/patientPharmacy.slice";
import { useForm } from "react-hook-form";

export default function PatientPharmacyInfo() {
  const [deleteId, setDeleteId] = useState("");
  const [editing, setEditing] = useState(false);
  const [showPharmacyId, setShowPharmacyId] = useState();
  const [selectedPharmacy, setSelectedPharmacy] = useState({});
  const [open, setOpen] = useState(false);
  const [searchPharmacyName, setSearchPharmacyName] = useState();
  const { patientPharmacy } = useSelector((state) => state || {});
  const { patientAppointedDetail } = useSelector((state) => state.appointment);

  const { register, handleSubmit, setValue, reset } = useForm();
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));

  const dispatch = useDispatch();

  const moveToNext = () => {
    const finalData = {
      PatientId: slotDescription?.patientId,
    };
    dispatch(getPatientPharmacyData(finalData));
    setEditing(false);
  };

  function createPharmacyData(submittedData) {
    const finalData = {
      pharmacyId:
        showPharmacyId || patientPharmacy?.patientPharmacyData?.pharmacyId || 0,
      pharmacyEmail: submittedData?.pharmacyEmail,
      pharmacyName:
        searchPharmacyName ||
        patientPharmacy?.patientPharmacyData?.pharmacyName ||
        "",
      pharmacyLocation:
        submittedData?.pharmacyLocation ||
        patientPharmacy?.patientPharmacyData?.pharmacyLocation ||
        "",
      country:
        submittedData?.country ||
        patientPharmacy?.patientPharmacyData?.country ||
        "",
      ig_xcord: "",
      ig_ycord: "",
      userId: patientAppointedDetail?.detailDTO?.patientId,
    };
    if (patientPharmacy?.patientPharmacyData?.pharmacyId) {
      dispatch(createPatientPharmacy({ finalData, moveToNext }));
    } else {
      dispatch(createPatientPharmacy({ finalData, moveToNext }));
    }
    const selectedPharmacyData = patientPharmacy?.allPatientPharmacyData?.find(
      (item) => item.pharmacyName === searchPharmacyName
    );

    if (selectedPharmacyData) {
      setValue("pharmacyLocation", selectedPharmacyData.pharmacyLocation);
      setValue("country", selectedPharmacyData.country);
      setValue("pharmacyEmail", selectedPharmacyData.pharmacyEmail);
      setSelectedPharmacy(selectedPharmacyData);
    }
  }

  const onClear = () => {
    setValue("pharmacyLocation", "");
    setValue("country", "");
    setSelectedPharmacy({});
  };
  const handleOnSearch = (string, results) => {
    if (results) {
      setValue("pharmacyLocation", results?.location);
      setValue("country", results?.country);
      setSelectedPharmacy(results);
      setShowPharmacyId(results?.id);
    } else {
      setValue("pharmacyLocation", "");
      setValue("country", "");
      setSelectedPharmacy({});
    }
  };

  const handleOnSelect = (item) => {
    if (item) {
      setSearchPharmacyName(item?.name);
      setValue("pharmacyLocation", item?.location);
      setValue("country", item?.country);
      setSelectedPharmacy(item);
      setShowPharmacyId(item?.id);
    }
  };

  const formatResult = (item) => {
    return (
      <>
        <span
          style={{ display: "block", textAlign: "left" }}
          className="fw-bold text-wrap"
        >
          {item?.name}
        </span>
        <span
          style={{ display: "block", textAlign: "left", fontSize: "15px" }}
          className="text-wrap"
        >
          {item?.location}
        </span>
      </>
    );
  };

  const onCreateSuccess = () => {
    const finalData = {
      patientId: slotDescription?.patientId,
    };
    dispatch(getPatientPharmacyData(finalData));
    setEditing(false);
    reset();
  };
  function deletePharmacyData() {
    const finalData = {
      patientId: deleteId,
    };
    dispatch(deletePatientPharmacy({ finalData, onCreateSuccess }));
  }
  return (
    <>
      <Form onSubmit={handleSubmit(createPharmacyData)}>
        <Row>
          <Col lg={12}>
            <div className="d-flex justify-content-between align-items-baseline">
              <span className="w-100">
                <Form.Label className="fw-bold">Pharmacy Name</Form.Label>
                <Form.Group>
                  {patientPharmacy?.patientPharmacyData?.pharmacyName &&
                  !editing ? (
                    <p>{patientPharmacy?.patientPharmacyData?.pharmacyName}</p>
                  ) : (
                    <ReactSearchAutocomplete
                      items={patientPharmacy?.allPatientPharmacyData?.map(
                        (option) => {
                          return {
                            id: option?.pharmacyId,
                            name: option?.pharmacyName,
                            location: option?.pharmacyLocation,
                            country: option?.country,
                            display: `${option?.pharmacyName} - ${option?.pharmacyLocation}`,
                          };
                        }
                      )}
                      onClear={onClear}
                      onSearch={handleOnSearch}
                      onSelect={handleOnSelect}
                      formatResult={formatResult}
                      autoFocus
                      placeholder="Select Pharmacy Name"
                      inputSearchString={
                        patientPharmacy?.patientPharmacyData?.pharmacyName ?? ""
                      }
                      styling={{
                        height: "46px",
                        border: "1px solid #dee2e6",
                        borderRadius: "0.375rem",
                        boxShadow: "none",
                      }}
                      className="auto__search-field"
                    />
                  )}
                  <datalist id="pharmacyNameList">
                    {patientPharmacy?.allPatientPharmacyData?.map((item) => (
                      <option key={item?.pharmacyId} value={item?.pharmacyName}>
                        {item?.pharmacyLocation}
                      </option>
                    ))}
                  </datalist>
                </Form.Group>
              </span>
              {patientPharmacy?.patientPharmacyData?.pharmacyId && !editing && (
                <div>
                  <div
                    className="d-flex justify-content-center align-items-center mb-2"
                    style={{
                      height: "29px",
                      width: "32px",
                      backgroundColor: "#E1EBFF",
                      borderRadius: "5px",
                      cursor: "pointer",
                    }}
                    onClick={() => setEditing(true)}
                  >
                    <BsPencilSquare
                      style={{
                        color: "#2269F2",
                        fontSize: "18px",
                      }}
                    />
                  </div>
                  <div
                    className="d-flex justify-content-center align-items-center"
                    style={{
                      height: "29px",
                      width: "32px",
                      backgroundColor: "#FFDADD",
                      borderRadius: "5px",
                      cursor: "pointer",
                    }}
                    onClick={() => {
                      setDeleteId(slotDescription?.patientId);
                      setOpen(true);
                    }}
                  >
                    <RiDeleteBin5Fill
                      style={{
                        color: "#E63946",
                        fontSize: "18px",
                      }}
                    />
                  </div>
                </div>
              )}
            </div>
            <Form.Group>
              <Form.Label className="fw-bold mt-2">Address</Form.Label>
              {patientPharmacy?.patientPharmacyData?.pharmacyLocation &&
              !editing ? (
                <p>{patientPharmacy?.patientPharmacyData?.pharmacyLocation}</p>
              ) : (
                <Form.Control
                  type="text"
                  placeholder="Select Address"
                  {...register("pharmacyLocation")}
                  defaultValue={
                    patientPharmacy?.patientPharmacyData?.pharmacyLocation
                      ? patientPharmacy?.patientPharmacyData?.pharmacyLocation
                      : ""
                  }
                  className={`${
                    !patientPharmacy?.allPatientPharmacyData && "bg-transparent"
                  }`}
                  readOnly={
                    patientPharmacy?.patientPharmacyData?.pharmacyLocation &&
                    editing
                      ? false
                      : patientPharmacy?.patientPharmacyData?.pharmacyLocation
                      ? true
                      : false
                  }
                  maxLength={250}
                  disabled={!patientPharmacy?.allPatientPharmacyData}
                />
              )}
            </Form.Group>
            <Form.Group>
              <Form.Label className="fw-bold mt-2">County</Form.Label>
              {patientPharmacy?.patientPharmacyData?.country &&
              editing === false ? (
                <p>{patientPharmacy?.patientPharmacyData?.country}</p>
              ) : (
                <Form.Control
                  type="text"
                  placeholder="County"
                  {...register("country")}
                  defaultValue={
                    patientPharmacy?.patientPharmacyData?.country ?? ""
                  }
                  readOnly={
                    patientPharmacy?.patientPharmacyData?.country && editing
                      ? false
                      : patientPharmacy?.patientPharmacyData?.country
                      ? true
                      : false
                  }
                  className={`${
                    !patientPharmacy?.allPatientPharmacyData && "bg-transparent"
                  }`}
                  maxLength={250}
                  disabled={!patientPharmacy?.allPatientPharmacyData}
                />
              )}
            </Form.Group>

            <Form.Group>
              <Form.Label className="fw-bold mt-2">Pharmacy Email</Form.Label>
              {patientPharmacy?.patientPharmacyData?.pharmacyEmail &&
              !editing ? (
                <p>{patientPharmacy?.patientPharmacyData?.pharmacyEmail}</p>
              ) : (
                <Form.Control
                  type="email"
                  placeholder="Pharmacy Email"
                  {...register("pharmacyEmail")}
                  defaultValue={
                    patientPharmacy?.patientPharmacyData?.pharmacyEmail ?? ""
                  }
                  maxLength={250}
                />
              )}
            </Form.Group>

            {(!patientPharmacy?.patientPharmacyData?.pharmacyId ||
              editing ||
              !patientPharmacy?.patientPharmacyData?.pharmacyEmail) && (
              <Button
                className="px-3 mt-3 primary_bg float-right"
                size="sm"
                type="submit"
              >
                {patientPharmacy?.patientPharmacyData?.pharmacyId ||
                !patientPharmacy?.patientPharmacyData?.pharmacyEmail
                  ? "Update "
                  : "Add "}
                Pharmacy
              </Button>
            )}
          </Col>
        </Row>
      </Form>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Pharmacy"}
        title={"this pharmacy"}
        removeFunc={deletePharmacyData}
      />
    </>
  );
}
